
import { useState, useEffect, useRef } from 'react';
import { Redirect } from 'react-router-dom';
import FormElement from './FormElement';
import FormElementCategory from './FormElementCategory';
import { FormContext } from './FormContext';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
import axios from 'axios'
import CheckSurveyTypeResponse from './CheckSurveyTypeResponse';
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";



//hey i will use react hook here
const CompleteSurvey = (props) => {

  
  const form = useRef();
  const checkBtn = useRef();

  const [Get_Survey_Details, Set_Survey_Details] = useState({});
  const [successful, setSuccessful] = useState(false);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  

  // Call_Survey_Details form localStorage
  const Surveydetails = JSON.parse(localStorage.getItem('Surveydetails'));
  const Call_Survey_Details = Surveydetails.form;


  
  useEffect(() => {
    // Call_Survey_Details form localStorage
    const Surveydetails = JSON.parse(localStorage.getItem('Surveydetails'));
    const Call_Survey_Details = Surveydetails.form;
    Set_Survey_Details(Call_Survey_Details)
       

  }, [])

  const handleSubmit = (event) => {
    event.preventDefault();

    setMessage("");
    setSuccessful(false);

    setMessage("");
    setLoading(true);


    form.current.validateAll();

    //get the user from the localstorage
    const mytoken = JSON.parse(localStorage.getItem('user'));
    const token = mytoken.token;
    // const state= mytoken

    //get single survey details
    const Surveydetails = JSON.parse(localStorage.getItem('Surveydetails'));

    //get the survey Pk  to send as require survey_pk
    const SurveydetailsPk = Surveydetails.pk

    //Save all user responses of the survey  to LocalStorage
    const to_Send = JSON.parse(localStorage.getItem('Response_to_Send'))

    // returns an array of a given object's own enumerable property values
    const I_to_Send = Object.values(to_Send);

    //user state_pk
    const state_pk = localStorage.getItem('state_pk');
    const lga_pk = localStorage.getItem('lga_pk');

    //Response Body to send to Mallam Ahmad
    const Responsebody =
    {
      survey_pk: SurveydetailsPk,
      lga_pk: lga_pk,
      state_pk: state_pk,
      form: I_to_Send
    }

    if (checkBtn.current.context._errors.length === 0) {

      // console.log(Responsebody);
      axios.post("https://gfso.chinikiguard.com/survey/api/create/response/", Responsebody,
        {
          headers:
          {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
          },
        })
        .then(res => {

          if (res.data) {
            localStorage.setItem("Response_Success", JSON.stringify(res.data));
          }
          setMessage(res.data.survey_title);
          setSuccessful(true);
        },
          (error) => {
            const resMessage =
              (error.response &&
                error.response.data.detail &&
                error.response.data.detail) ||
              error.survey_title ||
              error.toString();

            setMessage(resMessage);
            setSuccessful(false);
          }
        );
      // console.log(Get_Survey_Details)
    }
  }

  //this handle the change user typing
  const handleChange = (id, event) => {

    const newElements = { ...Get_Survey_Details }
    const arr = [...Get_Survey_Details];


    //convert the categories objects to array
    for (let n = 0; n < newElements.length; n++) {
      arr.push(newElements[n]);
    }

    //first array to loop through the categories
    for (let i = 0; i < arr.length; i++) {
      const category = arr[i];

      //second array to loop through the field
      for (let j = 0; j < category.fields.length; j++) {
        const field = category.fields[j];

        //check the pk for each field
        const { pk } = field;
        if (id === pk) {
          field['value'] = event.target.value;
        }
      }

      //first array to loop through the SubCategories
      for (let h = 0; h < category.sub_categories.length; h++) {
        const SubCategory = category.sub_categories[h];

        //second array to loop through the fields of SubCategory
        for (let j = 0; j < SubCategory.fields.length; j++) {
          const FormElementCategory = SubCategory.fields[j];
          // console.log(FormElementCategory)


          //check the pk for each field
          const { pk } = FormElementCategory;
          if (id === pk) {
            FormElementCategory['value'] = event.target.value;
          }
        }
      }
    }
    //sub_categories

    //Save the result to the localStorage
    localStorage.setItem("Response_to_Send", JSON.stringify(Get_Survey_Details));
  }

  if (!localStorage.getItem('user')) {
    return (<Redirect to={'/login'} />)
  }

  return (

    <FormContext.Provider value={{ handleChange }}>
      <Header />
      <div className="content-wrapper" style={{ minHeight: 1602 }}>
        <Menu /><br />
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              {/* <div className="card card-"> */}
              {/* left column */}
              
              {Surveydetails.form.length ? 

              <div className="col-md-12">
                <Form onSubmit={handleSubmit} ref={form}>
                  {!successful && (
                    <div>
                      <center><h2 class="mt-4 mb-2">{Surveydetails.title}</h2></center>
                      <CheckSurveyTypeResponse />
                      {Call_Survey_Details.map(myfield => (
                        <div className="card card-">
                          <div className="card-header">
                            <h3 className="card-title">{myfield.name}</h3>
                          </div>
                          <div className="card-body">
                            {/* <h6 className="">{myfield.type}</h6> */}
                            <div className="row">
                              {
                                myfield ? myfield.fields.map((field, i) => <FormElement key={i} field={field} />)
                                  : null
                              }
                            </div>
                          </div>
                          {myfield.sub_categories.map(subCategory => (
                            <div>
                              <h6 className="">{subCategory.name}</h6>
                              <div className="col-12">
                                <div className="row">
                                  {
                                    subCategory ? subCategory.fields.map((field_category, i) => <FormElementCategory key={i} field_category={field_category} />)
                                     : null
                                  }
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>
                      ))}
                      <div className="form-group">
                        {/* &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; */}
                        <center> <button className="btn btn-primary btn-lg " disabled={loading}>
                          {loading && (
                            <span className="spinner-grow spinner-grow-sm"></span>
                          )}
                          <span>Submit</span>
                        </button>
                        </center>
                      </div>
                    </div>
                  )}
                  <h3>
                    <center>
                      {message && (
                        <div className="form-group">
                          <div
                            className={
                              successful ? "alert alert-success" : "alert alert-danger"
                            }
                            role="alert"
                          >
                            {message}
                          </div>
                        </div>
                      )}
                    </center>

                  </h3>

                  <CheckButton style={{ display: "none" }} ref={checkBtn} />
                </Form>
              </div>
: <center><h2>No  Survey Template Available</h2></center>
                                        
}
            </div>
            {/* </div> */}
          </div>
        </section>
      </div>
      <Footer />
    </FormContext.Provider>
  );
}

export default CompleteSurvey;

