import React, { Component } from 'react'
import { Link, Switch } from 'react-router-dom';
import Logo from '../cbn.png'
import authService from '../services/auth.service';
import axios from 'axios'


class Menu extends Component {

    constructor(props) {
        super(props);

        this.state = {
            
            Group1_Branch: false,
            Group2: false,
            Respondent: false,
            Approver: false,
            Data_Management_team:false,
            current_User: undefined,
            NewUser:false,
            myloading: true,
            ReturnedUser:false
        };
    }



    componentDidMount() {
        const script = document.createElement("script");

        script.src = "./plugins/overlayScrollbars/js/jquery.overlayScrollbars.min.js";
        script.async = true;

        document.body.appendChild(script);
        //user  stored user information (including JWT) from AuthService class
        const token = JSON.parse(localStorage.getItem('token'));
        const tempoUser = JSON.parse(localStorage.getItem('tempoUser'));
        const user = authService.getCurrentUser();
        // const SuccessMessage = "User Created Successfully.";

        if (tempoUser) {

        this.setState({
            NewUser:tempoUser.user_groups.includes("NewUser")
        })
    }
        //check User Group
        if (user) {
            this.setState({
                current_User: user.profile,
                ReturnedUser:user.profile.user_groups.includes("NewUser"),
                Group1_Branch: user.profile.user_groups.includes("Group 1/Branch"),
                Group2: user.profile.user_groups.includes("Group 2"),
                Respondent: user.profile.user_groups.includes("Respondent"),
                Approver: user.profile.user_groups.includes("Approver"),
                Data_Management_team: user.profile.user_groups.includes("Data Management team"),           
            });

        // const token = user.token;
        axios.get("https://gfso.chinikiguard.com/survey/api/list/survey/?approved=yes&deployed=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    ApprovedSurveys: res.data.length,
                    myloading: false,
                    warningLoad: true,
                });
            })

            axios.get("https://gfso.chinikiguard.com/survey/api/list/survey/?deployed=no&rejected=yes",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    RejectedSurveys: res.data.length,

                    myloading: false,
                    warningLoad: true,
                });
            })

            //for template

            axios.get(`https://gfso.chinikiguard.com/survey/api/list/survey/?has_template=yes&approved_template=yes&deployed=no`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    ApprovedTemplate: res.data.length,
                    myloading: false,
                    warningLoad: true,
                });
            })

            axios.get(`https://gfso.chinikiguard.com/survey/api/list/survey/?has_template=yes&rejected_template=yes&deployed=no&rejected=yes`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    RejectedTemplate: res.data.length,
                    myloading: false,
                    warningLoad: true,
                });
            })
            axios.get(`https://gfso.chinikiguard.com/survey/api/list/survey/?has_template=yes&approved=yes&approved_template=yes&deployed=no`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    Pendingdeployement: res.data.length,
                    myloading: false,
                    warningLoad: true,
                });
            })
            axios.get(`https://gfso.chinikiguard.com/survey/api/list/survey/?approved=no&deployed=no&rejected=no`,
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    PendingApprovalSurvey: res.data.length,
                    myloading: false,
                    warningLoad: true,
                });
            })
            axios.get("https://gfso.chinikiguard.com/survey/api/list/survey/?has_template=yes&approved_template=no&rejected=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                this.setState({
                    PendingApprovalTemplate: res.data.length,
                    myloading: false,
                    warningLoad: true,
                });
            })
            

        }
    }

    componentWillMount() {
        const token = JSON.parse(localStorage.getItem('token'));

    const Check=JSON.parse(localStorage.getItem('user'));
            const user_type=Check.profile.user_type
            const lga =Check.profile.lga
            const state =Check.profile.state

            switch (user_type) {
                case 'sg_user':
                    return   axios.get(`https://gfso.chinikiguard.com/survey/api/list/survey/?deployed=yes&status=open&states=${state}`,
                    {
                        headers:
                        {
                            'Authorization': `Token ${token}`,
                            'Access-Control-Allow-Origin': '*',
                            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                            'Access-Control-Allow-Credentials': true
                        },
                    })
                    .then(res => {
                        this.setState({
                            AvailableSurvey: res.data.length,
                            myloading: false,
                            warningLoad: true,
                        });
                    })
             
                case 'lg_user':
                    return  axios.get(`https://gfso.chinikiguard.com/survey/api/list/survey/?deployed=yes&status=open&lgas=${lga}`,
                    {
                        headers:
                        {
                            'Authorization': `Token ${token}`,
                            'Access-Control-Allow-Origin': '*',
                            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                            'Access-Control-Allow-Credentials': true
                        },
                    })
                    .then(res => {
                        this.setState({
                            AvailableSurvey: res.data.length,
                            myloading: false,
                            warningLoad: true,
                        });
                    })
        
                default:
                    return null;
            }
        
        }  
           


    render() {
        const { NewUser,ReturnedUser, current_User, Group1_Branch, Group2,Respondent,Approver,Data_Management_team } = this.state;


        return (

            <Switch>
                
                {/* {current_User ||NewUser && ( */}


                    <aside className="main-sidebar sidebar-light-success elevation-4 ">

                        {/* Brand Logo */}
                        <Link to="/dashboard" className="brand-link">
                            <center> <img src={Logo} height="100px" alt=""/></center><br />
                            <center> <span className="brand-text font-weight-light text-yellow"><h3><b>GFSO</b></h3></span></center>
                        </Link>

                        <div className="sidebar">

                            {/* Sidebar Menu */}
                            <nav className="mt-2">
                            {Group1_Branch && (

                                <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                    
                                    <li className="nav-item has-treeview menu-open">

                                        <Link to="/dashboard" className="nav-link active">
                                            <i className="nav-icon fas fa-home" />
                                            <p>
                                                Dashboard
                                        </p>
                                        </Link>

                                       
                                        {/* group1 */}
                                        
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/RespondentInfo" className="nav-link">
                                                    <i class="nav-icon fas fa-info text-green"></i>
                                                    <p>
                                                    Update Respondent 
                                                 </p>
                                                </Link>
                                            </li>
                                            
                                        </ul>
                                        {/* <hr/> */}
                                        <h1 className="nav-header">SURVEY</h1>
                                        {/* <hr/> */}

                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/Deploy" className="nav-link">
                                                    <i class="nav-icon fas fa-plus text-green"></i>
                                                    <p>
                                                    
                                                    Create Survey 
</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            
                                            <li class="nav-item has-treeview">
                                                    <Link to="/ApproveThisSurvey" className="nav-link">
                                                        <i class="nav-icon fas fa-warning text-warning"></i>
                                                        <p>
                                                        
    
                                                     Pending Approval
                                                     {this.state.PendingApprovalSurvey
                                                      ?this.state.PendingApprovalSurvey!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.PendingApprovalSurvey}
                                                    </span>
                                                    :""}    
    </p>
                                                    </Link>
                                                </li>
                                           
                                                </ul> 
                                        <ul class="nav nav-treeview">
                                            
                                        <li class="nav-item has-treeview">
                                                <Link to="/ApprovedSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-check text-green"></i>
                                                    <p>
                                                     
                                                  Approved Surveys 
                                                  {this.state.ApprovedSurveys
                                                      ?this.state.ApprovedSurveys!=0

                                                     && <span className="badge badge-success navbar-badge">
                                                    {this.state.ApprovedSurveys}
                                                    </span>
                                                    :""}

</p>
                                                </Link>
                                            </li>
                                       
                                            </ul> 
                                            
                                            
                                        <ul class="nav nav-treeview">
                                            
                                            <li class="nav-item has-treeview">
                                                    <Link to="/RejectedSurvey" className="nav-link">
                                                        <i class="nav-icon fas fa-times text-red"></i>
                                                        <p>
                                                         
                                                      Rejected Surveys 
                                                      {this.state.RejectedSurveys
                                                      ?this.state.RejectedSurveys!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.RejectedSurveys}
                                                    </span>
                                                    :""}
                                                      
    </p>
                                                    </Link>
                                                </li>
                                           
                                                </ul> 
                                                
                                                <ul class="nav nav-treeview">
                                            
                                            <li class="nav-item has-treeview">
                                                    <Link  to="/SurveyToDeploy"className="nav-link">
                                                        <i class="nav-icon fas fa-clock-o text-green"></i>
                                                        <p>
                                                         
                                                        Pending Deployement
                                                        {this.state.Pendingdeployement
                                                      ?this.state.Pendingdeployement!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.Pendingdeployement}
                                                    </span>
                                                    :""}
                                                      

    </p>

                                                    </Link>
                                                </li>
                                           
                                                </ul>  
                                                <ul class="nav nav-treeview">
                                            
                                            <li class="nav-item has-treeview">
                                                    <Link  to="/surveys"className="nav-link">
                                                        <i class="nav-icon fas fa-chart-line text-green"></i>
                                                        <p>
                                                         
                                                         Surveys Progress 
    
    </p>
                                                    </Link>
                                                </li>
                                           
                                                </ul> 
                                                {/* <hr/>  */}
                                                <h1 className="nav-header">TEMPLATE</h1>
                                                {/* <hr/> */}
                                                <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ApproveThisTemplate" className="nav-link">
                                                    <i class="nav-icon fas fa-warning text-warning"></i>
                                                    <p>
                                                     
                                                    Pending Approval 
                                                    
                                                    {this.state.PendingApprovalTemplate
                                                      ?this.state.PendingApprovalTemplate!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.PendingApprovalTemplate}
                                                    </span>
                                                    :""}
</p>
                                                </Link>
                                            </li>
                                        </ul> 

                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ViewSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-plus text-green"></i>
                                                    <p>
                                                     
                                                    Deploy Template
</p>
                                                </Link>
                                            </li>
                                        </ul> 
                                        
                                        <ul class="nav nav-treeview">
                                            
                                        <li class="nav-item has-treeview">
                                                <Link to="/ApprovedTemplate" className="nav-link">
                                                    <i class="nav-icon fas fa-check text-green"></i>
                                                    <p>
                                                    

                                                  Approved 
                                                  {this.state.ApprovedTemplate
                                                      ?this.state.ApprovedTemplate!=0

                                                     && <span className="badge badge-success navbar-badge">
                                                    {this.state.ApprovedTemplate}
                                                    </span>
                                                    :""}
</p>
                                                </Link>
                                            </li>
                                       
                                            </ul>  
                                        <ul class="nav nav-treeview">
                                            
                                            <li class="nav-item has-treeview">
                                                    <Link to="/RejectedTemplate" className="nav-link">
                                                        <i class="nav-icon fas fa-times text-red"></i>
                                                        <p>
                                                         
                                                      Rejected  
                                                      {this.state.RejectedTemplate
                                                      ?this.state.RejectedTemplate!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.RejectedTemplate}
                                                    </span>
                                                    :""}    </p>
                                                    </Link>
                                                </li>
                                           
                                                </ul>  
                                            
                                    </li>
                                </ul>
                            )}


                                {NewUser && (
                                      <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                      <li className="nav-item has-treeview menu-open">
  
                                    <ul class="nav nav-treeview">
                                        <li class="nav-item has-treeview">
                                            <Link to="/UserUploads" className="nav-link">
                                                <i class="nav-icon fas fa-paperclip text-warning"></i>
                                                <p>
                                                    Supporting Docs

                                          </p>
                                            </Link>
                                            </li>
                                    </ul> </li>
                                    </ul>

                                )}
                                  {/* group 2 */}

                                {Group2 && (
                                    
                                <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                    <li className="nav-item has-treeview menu-open">

                                        <Link to="/dashboard" className="nav-link active bg-primary">
                                            <i className="nav-icon fas fa-home" />
                                            <p>
                                                Dashboard
    </p>
                                        </Link>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/RespondentList" className="nav-link">
                                                <i class="nav-icon fas fa-check-circle  text-primary"></i>
                                                    <p>
                                                  Approve  Respondent  

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        {/* <hr/> */}
                                        <h1 className="nav-header">SURVEY</h1>
                                        {/* <hr/> */}

                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ListToApprove" className="nav-link">
                                                    <i class="nav-icon fas fa-warning  text-warning"></i>
                                                    <p>
                                                    Pending Approval
                                                    
                                                    {this.state.PendingApprovalSurvey
                                                      ?this.state.PendingApprovalSurvey!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.PendingApprovalSurvey}
                                                    </span>
                                                    :""}

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/SurveyApproved" className="nav-link">
                                                    <i class="nav-icon fas fa-check  text-green"></i>
                                                    <p>
                                                     Approved
                                                     {this.state.ApprovedSurveys
                                                      ?this.state.ApprovedSurveys!=0

                                                     && <span className="badge badge-success navbar-badge">
                                                    {this.state.ApprovedSurveys}
                                                    </span>
                                                    :""}

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/SurveyRejected" className="nav-link">
                                                    <i class="nav-icon fas fa-times  text-danger"></i>
                                                    <p>
                                                    Rejected
                                                    {this.state.RejectedSurveys
                                                      ?this.state.RejectedSurveys!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.RejectedSurveys}
                                                    </span>
                                                    :""}

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        {/* <hr/> */}
                                        <h1 className="nav-header">TEMPLATE</h1>
                                        {/* <hr/> */}

                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/TemplateToApprove" className="nav-link">
                                                    <i class="nav-icon fas fa-warning  text-warning"></i>
                                                    <p>
                                                    Pending Approval 
                                                    
                                                    {this.state.PendingApprovalTemplate
                                                      ?this.state.PendingApprovalTemplate!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.PendingApprovalTemplate}
                                                    </span>
                                                    :""}

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/TemplateApproved" className="nav-link">
                                                    <i class="nav-icon fas fa-check  text-green"></i>
                                                    <p>
                                                    Approved 
                                                    {this.state.ApprovedTemplate
                                                      ?this.state.ApprovedTemplate!=0

                                                     && <span className="badge badge-success navbar-badge">
                                                    {this.state.ApprovedTemplate}
                                                    </span>
                                                    :""}   
</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/TemplateRejected" className="nav-link">
                                                    <i class="nav-icon fas fa-times  text-danger"></i>
                                                    <p>
                                                    Rejected 
                                                    {this.state.RejectedTemplate
                                                      ?this.state.RejectedTemplate!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.RejectedTemplate}
                                                    </span>
                                                    :""}   
</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        {/* <hr/> */}
                                        <h1 className="nav-header">RESPONSES</h1>
{/* <hr/> */}
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ValidateResponse" className="nav-link">
                                                <i class="nav-icon fas fa-check-circle  text-primary"></i>
                                                    <p>
                                                Validate Responses

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                       
                                    </li>

                                </ul>
                                )}
                                
                            
                                {ReturnedUser && (

                                    <ul class="nav nav-treeview">
                                        <li class="nav-item has-treeview">
                                            <Link to="/UserUploads" className="nav-link">
                                                <i class="nav-icon fas fa-paperclip text-warning"></i>
                                                <p>
                                                    Supporting Docs

</p>
                                            </Link>
                                        </li>
                                    </ul>
                                )}
                                {Respondent && (

                                    <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                        <li className="nav-item has-treeview menu-open">

                                            <Link to="/dashboard" className="nav-link active bg-warning">
                                                <i className="nav-icon fas fa-home" />
                                                <p>
                                                    Dashboard
</p>
                                            </Link>
                                            
                                            <ul class="nav nav-treeview">
                                                <li class="nav-item has-treeview">
                                                    <Link to="/AvailableSurvey" className="nav-link">
                                                        <i class="nav-icon fas fa-bullhorn text-warning"></i>
                                                        <p>
                                                            Available Survey
                                                            
                                                            {this.state.AvailableSurvey
                                                      ?this.state.AvailableSurvey!=0

                                                     && <span className="badge badge-danger navbar-badge">
                                                    {this.state.AvailableSurvey}
                                                    </span>
                                                    :""}  

</p>
                                                    </Link>
                                                </li>
                                            </ul>

                                        </li>

                                    </ul>
                                )}
                                {Approver && (

                                    <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                        <li className="nav-item has-treeview menu-open">

                                            <Link to="/dashboard" className="nav-link active bg-info">
                                                <i className="nav-icon fas fa-home" />
                                                <p>
                                                    Dashboard
</p>
                                            </Link>
                                            <ul class="nav nav-treeview">
                                                <li class="nav-item has-treeview">
                                                    <Link to="/ApproveResponse" className="nav-link">
                                                        <i class="nav-icon fas fa-paperclip text-info"></i>
                                                        <p>
                                                        Approve response

</p>
                                                    </Link>
                                                </li>
                                            </ul>
                                            <ul class="nav nav-treeview">
                                                <li class="nav-item has-treeview">
                                                    <Link to="/ApproveResponse" className="nav-link">
                                                        <i class="nav-icon fas fa-bullhorn text-info"></i>
                                                        <p>
                                                            Generate Mgt. Reports

</p>
                                                    </Link>
                                                </li>
                                            </ul>

                                        </li>

                                    </ul>
                                )}
                                {Data_Management_team && (

                                    <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                        <li className="nav-item has-treeview menu-open">

                                            <Link to="/dashboard" className="nav-link active bg-pink">
                                                <i className="nav-icon fas fa-home" />
                                                <p>
                                                    Dashboard
</p>
                                            </Link>
                                            <ul class="nav nav-treeview">
                                                <li class="nav-item has-treeview">
                                                    <Link to="/Profile" className="nav-link">
                                                        <i class="nav-icon fas fa-paperclip text-pink"></i>
                                                        <p>
                                                        Develop Capability

</p>
                                                    </Link>
                                                </li>
                                            </ul>
                                            <ul class="nav nav-treeview">
                                                <li class="nav-item has-treeview">
                                                    <Link to="/Profile" className="nav-link">
                                                        <i class="nav-icon fas fa-bullhorn text-pink"></i>
                                                        <p>
                                                           Manage Reports

</p>
                                                    </Link>
                                                </li>
                                            </ul>

                                        </li>

                                    </ul>
                                )}
                            </nav>
                            {/* /.sidebar-menu */}
                        </div>
                        {/* /.sidebar */}



                    </aside>

                {/* )} */}
            </Switch>

        )
    }

}


export default Menu;