import React, { Component } from "react";
import { Link, Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'
import authService from "../services/auth.service";
import Logo from '../cbn.png'


const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

export default class Notifications extends Component {

  constructor(props) {
    super(props);


    this.state = {
      message: "",
      loading: true,
      AdoptSurveymessage: "",
      AdoptSurveysuccessful: false,
      AdoptSurveyloading: false,
      survey: "",
      true:"False"

    };
  }



  componentDidMount() {
    if (!localStorage.getItem('user')) {
      return (<Redirect to={'/login'} />)
    }
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;
    axios.get('https://gfso.chinikiguard.com/core/api/user/notification/list/?read=no',
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
        },

      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("notification", JSON.stringify(res.data));
        }
        this.setState({
          notification: res.data.length,
          message: res.data,
          loading: false,

        });
        // console.log(res.data)                  

      })

  }

  ViewNotification(message){

    this.state.true= "True"

    const notifyId = message.pk
    // console.log(message.pk)
    // console.log(this.state.true)
    localStorage.setItem("notify_Update_ID", JSON.stringify(message.pk));
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;

    this.setState({
        loadingModal: true,
      });

    axios.get(`https://gfso.chinikiguard.com/core/api/user/notification/details/${notifyId}/`,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            window.location = "/ViewNotification"

            if (res.data) {
                localStorage.setItem("ApproveNotifications", JSON.stringify(res.data));
            }

            this.setState({
              ApproveThisSurvey: res.data,
              loadingModal: false,
            });

          })

          let formData = new FormData();
          formData.append('read', this.state.true);
          axios.patch(`https://gfso.chinikiguard.com/core/api/user/notification/update/${notifyId}/`,formData,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            if (res.data) {
                localStorage.setItem("Read", JSON.stringify(res.data));
            }

          })

  }


  render() {

    //check for user to access the page
    if (!localStorage.getItem('user')) {
      return (<Redirect to={'/login'} />)
    }
    //get the template details
    // const ApproveNotifications = JSON.parse(localStorage.getItem('ApproveNotifications'));


    return (
      <div>
        <Header />
        <div className="content-wrapper" style={{ minHeight: 1602 }}>
          <Menu /><br />
          <section className="content">
            <div className="row">
              <div className="col-md-3">
                <Link className="btn btn-success btn-block mb-3">Messages</Link>
               
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Folders</h3>
                    <div className="card-tools">
                      <button type="button" className="btn btn-tool" data-card-widget="collapse">
                        <i className="fas fa-minus" />
                      </button>
                    </div>
                  </div>
                  <div className="card-body p-0">
                    <ul className="nav nav-pills flex-column">
                      <li className="nav-item active">
                        <Link to="/Notifications" className="nav-link">
                          <i className="fas fa-inbox" /> Inbox
                <span className="badge bg-primary float-right">{this.state.notification}</span>
                        </Link>
                      </li>
                      <li className="nav-item">
                        <a href="#" className="nav-link">
                          <i className="far fa-envelope" /> Read
              </a>
                      </li>
                    
                      <li className="nav-item">
                        <a href="#" className="nav-link">
                          <i className="far fa-trash-alt" /> Trash
              </a>
                      </li>
                    </ul>
                  </div>
                  {/* /.card-body */}
                </div>
               
              </div>
              {/* /.col */}
              <div className="col-md-9">
                <div className="card card-success card-outline">
                  <div className="card-header">
                    <h3 className="card-title">Inbox</h3>
                    <div className="card-tools">
                      <div className="input-group input-group-sm">
                        <input type="text" className="form-control" placeholder="Search Mail" />
                        <div className="input-group-append">
                          <div className="btn btn-primary">
                            <i className="fas fa-search" />
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card-tools */}
                  </div>
                  {/* /.card-header */}
                  <div className="card-body p-0">
                    <div className="mailbox-controls">
                      {/* Check all button */}
                      <button type="button" className="btn btn-default btn-sm checkbox-toggle"><i className="far fa-square" />
                      </button>
                      <div className="btn-group">
                        <button type="button" className="btn btn-default btn-sm">
                          <i className="far fa-trash-alt" />
                        </button>
                        <button type="button" className="btn btn-default btn-sm">
                          <i className="fas fa-reply" />
                        </button>
                        <button type="button" className="btn btn-default btn-sm">
                          <i className="fas fa-share" />
                        </button>
                      </div>
                      {/* /.btn-group */}
                      <button type="button" className="btn btn-default btn-sm">
                        <i className="fas fa-sync-alt" />
                      </button>
                      <div className="float-right">
                        {this.state.notification}
              <div className="btn-group">
                          <button type="button" className="btn btn-default btn-sm">
                            <i className="fas fa-chevron-left" />
                          </button>
                          <button type="button" className="btn btn-default btn-sm">
                            <i className="fas fa-chevron-right" />
                          </button>
                        </div>
                        {/* /.btn-group */}
                      </div>
                      {/* /.float-right */}
                    </div>
                    <div className="table-responsive mailbox-messages">
                      {this.state.loading ? (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span> 
                        ) : (
                      <table className="table table-hover table-striped">
                        <tbody>
                        {this.state.message && this.state.message.map(message => (
                          <tr>
                            <td>
                              <div className="icheck-primary">
                                <input type="checkbox" defaultValue id="check1" />
                                <label htmlFor="check1" />
                              </div>
                            </td>
                            <td className="mailbox-star"><a href="#"><i className="fas fa-star text-warning" /></a></td>
                            <td className="mailbox-name">
                            <Link
                            onClick={this.ViewNotification.bind(this, message)}
                            >{message.api_message}</Link></td>
                            <td className="mailbox-subject"><b>{message.subject}</b> -         </td>
                            <td className="mailbox-attachment" />
                            <td className="mailbox-date">{message.created}</td>
                          </tr>
                        ))}
                           </tbody>
                      </table>
                        )}
                    </div>
                    {/* /.mail-box-messages */}
                  </div>
                  {/* /.card-body */}
                  <div className="card-footer p-0">
                    <div className="mailbox-controls">
                      {/* Check all button */}
                      <button type="button" className="btn btn-default btn-sm checkbox-toggle">
                        <i className="far fa-square" />
                      </button>
                      <div className="btn-group">
                        <button type="button" className="btn btn-default btn-sm">
                          <i className="far fa-trash-alt" />
                        </button>
                        <button type="button" className="btn btn-default btn-sm">
                          <i className="fas fa-reply" />
                        </button>
                        <button type="button" className="btn btn-default btn-sm">
                          <i className="fas fa-share" />
                        </button>
                      </div>
                      {/* /.btn-group */}
                      <button type="button" className="btn btn-default btn-sm">
                        <i className="fas fa-sync-alt" />
                      </button>
                      <div className="float-right">
                        1-50/200
              <div className="btn-group">
                          <button type="button" className="btn btn-default btn-sm">
                            <i className="fas fa-chevron-left" />
                          </button>
                          <button type="button" className="btn btn-default btn-sm">
                            <i className="fas fa-chevron-right" />
                          </button>
                        </div>
                        {/* /.btn-group */}
                      </div>
                      {/* /.float-right */}
                    </div>
                  </div>
                </div>
                {/* /.card */}
              </div>
              {/* /.col */}
            </div>
            {/* /.row */}
          </section>

        </div>
        <Footer />
      </div>
    );
  }
}



