import React from 'react'
import { Switch, Route } from 'react-router-dom';
import Dashboard from './Dashboard';
import Login from './Login';
import Landing from './Landing';
// import { FormBuilder } from './FormBuilder';
import ViewSurvey from './ViewSurvey';
import CompleteSurvey from './CompleteSurvey';
import CreateNewUser from './CreateNewUser';
import EditTemplate from './EditTemplate';
import Surveys from './Surveys';
import AvailableSurvey from './AvailableSurvey';
import CategoryView from './CategoryView';
import ListResponses from './ListResponses';
import ResponseTable from './ResponseTable';
import UserProfile from './UserProfile';
import ResponseByUser from './ResponseByUser';
import Deploy from './Deploy';
import ReviewRespondent from './ReviewRespondent';
import ListToApprove from './ListToApprove';
import ApproveTemplate from './ApproveTemplate';
import ApproveResponse from './ApproveResponse';
import ValidateResponse from './ValidateResponse';
import RespondentList from './RespondentList';
import ApproveSurvey from './ApproveSurvey';
import TemplateToApprove from './TemplateToApprove';
import ApprovedSurvey from './ApprovedSurvey';
import ApprovedTemplate from './ApprovedTemplate';
import RejectedTemplate from './RejectedTemplate';
import RejectedSurvey from './RejectedSurvey';
import RespondentInfo from './RespondentInfo';
import Notifications from './Notifications';
import SurveyToDeploy from './SurveyToDeploy';
import DeploySurvey from './DeploySurvey';
import SurveyRejected from './SurveyRejected';
import SurveyApproved from './SurveyApproved';
import TemplateApproved from './TemplateApproved';
import TemplateRejected from './TemplateRejected';
import ApproveThisTemplate from './ApproveThisTemplate';
import ApproveThisSurvey from './ApproveThisSurvey';
import ViewNotification from './ViewNotification';
import UploadSupporting from './UploadSupporting';
import ReviewResponder from './ReviewResponder';
import ApproveResponder from './ApproveResponder';







const  Content = () => {
    return (
       <Switch>
           <Route exact path="/" component={Landing}/>
           <Route  path="/landing" component={Landing}/>


           <Route exact path="/" component={Login}/>
           <Route  path="/login" component={Login}/>

 
           <Route exact path="/" component={Dashboard}/>
           <Route  path="/dashboard" component={Dashboard}/>

          
           {/* <Route exact path="/" component={FormBuilder}/>
           <Route  path="/FormBuilder" component={FormBuilder}/>

           */}
           <Route exact path="/" component={ViewSurvey}/>
           <Route  path="/ViewSurvey" component={ViewSurvey}/>
           
           <Route exact path="/" component={CompleteSurvey}/>
           <Route  path="/CompleteSurvey" component={CompleteSurvey}/>
           
           <Route exact path="/" component={CreateNewUser}/>
           <Route  path="/Register" component={CreateNewUser}/>

          <Route exact path="/" component={EditTemplate}/>
           <Route  path="/EditTemplate" component={EditTemplate}/>
           
           <Route exact path="/" component={Surveys}/>
           <Route  path="/Surveys" component={Surveys}/>
           
           <Route exact path="/" component={AvailableSurvey}/>
           <Route  path="/AvailableSurvey" component={AvailableSurvey}/>

           
           <Route exact path="/" component={CategoryView}/>
           <Route  path="/CategoryView" component={CategoryView}/>

           <Route exact path="/" component={ListResponses}/>
           <Route  path="/ListResponses" component={ListResponses}/>
           
           <Route exact path="/" component={ResponseTable}/>
           <Route  path="/ResponseTable" component={ResponseTable}/>

           <Route exact path="/" component={UserProfile}/>
           <Route  path="/Profile" component={UserProfile}/>

           <Route exact path="/" component={ResponseByUser}/>
           <Route  path="/ResponseByUser" component={ResponseByUser}/>

           <Route exact path="/" component={Deploy}/>
           <Route  path="/Deploy" component={Deploy}/>

           <Route exact path="/" component={ReviewRespondent}/>
           <Route  path="/ReviewRespondent" component={ReviewRespondent}/>

           <Route exact path="/" component={ListToApprove}/>
           <Route  path="/ListToApprove" component={ListToApprove}/>

           <Route exact path="/" component={ApproveTemplate}/>
           <Route  path="/ApproveTemplate" component={ApproveTemplate}/>

         
           <Route exact path="/" component={ApproveResponse}/>
           <Route  path="/ApproveResponse" component={ApproveResponse}/>

           <Route exact path="/" component={ValidateResponse}/>
           <Route  path="/ValidateResponse" component={ValidateResponse}/>

           <Route exact path="/" component={RespondentList}/>
           <Route  path="/RespondentList" component={RespondentList}/>

           <Route exact path="/" component={ApproveSurvey}/>
           <Route  path="/ApproveSurvey" component={ApproveSurvey}/>

           <Route exact path="/" component={TemplateToApprove}/>
           <Route  path="/TemplateToApprove" component={TemplateToApprove}/>

           <Route exact path="/" component={ApprovedSurvey}/>
           <Route  path="/ApprovedSurvey" component={ApprovedSurvey}/>

           <Route exact path="/" component={ApprovedTemplate}/>
           <Route  path="/ApprovedTemplate" component={ApprovedTemplate}/>

           <Route exact path="/" component={RejectedTemplate}/>
           <Route  path="/RejectedTemplate" component={RejectedTemplate}/>

           <Route exact path="/" component={RejectedSurvey}/>
           <Route  path="/RejectedSurvey" component={RejectedSurvey}/>

           <Route exact path="/" component={RespondentInfo}/>
           <Route  path="/RespondentInfo" component={RespondentInfo}/>

           <Route exact path="/" component={Notifications}/>
           <Route  path="/Notifications" component={Notifications}/>

           <Route exact path="/" component={SurveyToDeploy}/>
           <Route  path="/SurveyToDeploy" component={SurveyToDeploy}/>

           <Route exact path="/" component={DeploySurvey}/>
           <Route  path="/DeploySurvey" component={DeploySurvey}/>

           <Route exact path="/" component={SurveyRejected}/>
           <Route  path="/SurveyRejected" component={SurveyRejected}/>

           <Route exact path="/" component={SurveyApproved}/>
           <Route  path="/SurveyApproved" component={SurveyApproved}/>
           
           <Route exact path="/" component={TemplateApproved}/>
           <Route  path="/TemplateApproved" component={TemplateApproved}/>
 
           <Route exact path="/" component={TemplateRejected}/>
           <Route  path="/TemplateRejected" component={TemplateRejected}/>

           <Route exact path="/" component={ApproveThisTemplate}/>
           <Route  path="/ApproveThisTemplate" component={ApproveThisTemplate}/>

           <Route exact path="/" component={ApproveThisSurvey}/>
           <Route  path="/ApproveThisSurvey" component={ApproveThisSurvey}/>

           <Route exact path="/" component={ViewNotification}/>
           <Route  path="/ViewNotification" component={ViewNotification}/>

           <Route exact path="/" component={UploadSupporting}/>
           <Route  path="/UserUploads" component={UploadSupporting}/>

           <Route exact path="/" component={ReviewResponder}/>
           <Route  path="/ReviewResponder" component={ReviewResponder}/>

           <Route exact path="/" component={ApproveResponder}/>
           <Route  path="/ApproveResponder" component={ApproveResponder}/>

           
           
       </Switch>

       
    )
}


export default Content;
