
import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'
import MultiselectCheckbox from "react-multiselect-checkbox";
import Logo from '../cbn.png'
import authService from '../services/auth.service';




const required = value => {
    if (!value) {
        return (
            <div className="alert alert-danger" role="alert">
                This field is required!
            </div>
        );
    }
};

export default class EditTemplate extends Component {

    constructor(props) {
        super(props);
        this.onChangeTitle = this.onChangeTitle.bind(this);
        this.onChangeQuarter = this.onChangeQuarter.bind(this);
        this.onChangeStatus = this.onChangeStatus.bind(this);
        this.onChangeSurveyType = this.onChangeSurveyType.bind(this);
        this.onChangeStartDate = this.onChangeStartDate.bind(this);
        this.onChangeDueDate = this.onChangeDueDate.bind(this);
        this.onChangeYear = this.onChangeYear.bind(this);
        this.onChangeState = this.onChangeState.bind(this);
        this.onChangeLgas = this.onChangeLgas.bind(this);
        this.AdoptSurvey = this.AdoptSurvey.bind(this);

        this.state = {
            title: "",
            quarter: "",
            status: "",
            year: "",
            survey_type: "",
            start_date: "",
            due_date: "",
            loading: false,
            message: "",
            states: "",
            lgas: "",
            myloading: false,
            statesGov: false,
            lgasGov: false,
            othersGov: false,
            AdoptSurveymessage: "",
            AdoptSurveysuccessful: false,
            AdoptSurveyloading: false
        };
    }



    onChangeTitle(e) {
        this.setState({
            title: e.target.value
        });
    }

    onChangeQuarter(e) {
        this.setState({
            quarter: e.target.value
        });
    }

    onChangeYear(e) {
        this.setState({
            year: e.target.value
        });
    }
    onChangeStartDate(e) {
        this.setState({
            start_date: e.target.value
        });
    }
    onChangeDueDate(e) {
        this.setState({
            due_date: e.target.value
        });
    }
    onChangeSurveyType(e) {
        this.setState({
            survey_type: e.target.value
        });
    }

    onChangeStatus(e) {
        this.setState({
            status: e.target.value
        });
    }

    onChangeState(e) {
        this.setState({
            states: e.target.value
        });
    }
    onChangeLgas(e) {
        this.setState({
            lgas: e.target.value
        });
    }


    AdoptSurvey(e) {
        e.preventDefault();

        this.setState({
            AdoptSurveymessage: "",
            AdoptSurveysuccessful: false,
            AdoptSurveyloading: true
        });

        
            //check auth user
            const mytoken = JSON.parse(localStorage.getItem('user'));
            const token = mytoken.token;

           
            //convert the string to number from the [go] array i save to  localStorage for State
            const stop_here_for_state = localStorage.getItem('Checked_State');
            const jasus_for_state = stop_here_for_state
          
            //convert the string to number from the [go] array i save to localStorage for LGA
            const stop_here_for_lga = localStorage.getItem('Checked_LGA');
            const jasus_for_lga = stop_here_for_lga

           
            let formData = new FormData();
            formData.append('title', this.state.title);
            formData.append('quarter', this.state.quarter);
            formData.append('year', this.state.year);
            formData.append('survey_type', this.state.survey_type);
            formData.append('start_date', this.state.start_date);
            formData.append('due_date', this.state.due_date);
            formData.append('states', jasus_for_state);
            formData.append('lgas', jasus_for_lga);

            this.form.validateAll();
            if (this.checkBtn.context._errors.length === 0) {
    
            axios.post("https://gfso.chinikiguard.com/survey/api/create/survey/", 
            formData,
                {
                    headers:
                    {
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${token}`,
                        'Access-Control-Allow-Origin': '*',
                        'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                        'Access-Control-Allow-Credentials': true
                    },
                })
                .then(res => {
                    this.setState({
                        AdoptSurveymessage: res.data.detail,
                        AdoptSurveysuccessful: true,
                    });
                      console.log(res.data);

                    window.location = "/Deploy"
                    if (res.data) {
                        localStorage.setItem("AdoptedSurvey", JSON.stringify(res.data));
                    }
                },
                    error => {
                        const resMessage =
                            (error.response &&
                                error.response.data.message &&
                                error.response.data.message) ||
                            error.AdoptSurveyloading ||
                            error.toString();

                        this.setState({
                            AdoptSurveyloading: false,
                            message: resMessage
                        });
                    }
                );
        } else {
            this.setState({
                AdoptSurveyloading: false
            });
        }
    }

    AddField(Filter_with_name) {

     
        //select Target category
        localStorage.setItem("Category_to_AddField", JSON.stringify(Filter_with_name));
        localStorage.setItem("MyCategoryName", JSON.stringify(Filter_with_name.name));
        window.location = "/CategoryView"
    }

    componentDidMount() {

        if (!localStorage.getItem('user')) {
            return (<Redirect to={'/login'} />)
          }
        //reset to default state
        const Checked_State = 1
        localStorage.setItem("Checked_State", Checked_State);
        //reset to default state
        const Checked_LGA = 1
        localStorage.setItem("Checked_LGA", Checked_LGA);
        //call the States and LGAS at the first 
        const state = "Ahmad loophole";
        const user = authService.getCurrentUser();

        const token = user.token;
        axios.get(`https://gfso.chinikiguard.com/locations/api/list/state/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
                params:
                {
                    state: state
                }
            },
        )
            .then(res => {
                if (res.data) {
                    localStorage.setItem("List_Of_State", JSON.stringify(res.data));
                }
                // console.log(res.data)                  

            })
        axios.get(`https://gfso.chinikiguard.com/locations/api/list/lga/`,
            {
                headers:
                {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
                params:
                {
                    state: state
                }
            },
        )
            .then(res => {
                if (res.data) {
                    localStorage.setItem("List_Of_Lga", JSON.stringify(res.data));
                }

            });
        // console.log(res.data)   
    }

    componentDidUpdate(prevProps, prevState) {
        //reset to default state
        const Checked_State = 1
        localStorage.setItem("Checked_State", Checked_State);
        const Checked_LGA = 1 
        localStorage.setItem("Checked_LGA", Checked_LGA);
        // check for the value selected and show the related content
        try {
            if (prevState.survey_type !== this.state.survey_type) {
                const state = this.state.survey_type
                if (state) {
                    this.setState({
                        statesGov: state.includes("sg"),
                        lgasGov: state.includes("lg"),
                        othersGov: state.includes("other"),
                    });
                }
            }

        } catch (e) {

            console.log(e);
        }
    }


    render() {

        //check for user to access the page
        if (!localStorage.getItem('user')) {
            return (<Redirect to={'/login'} />)
          }
        const List_Of_States = JSON.parse(localStorage.getItem('List_Of_State'));
        const List_Of_lgas = JSON.parse(localStorage.getItem('List_Of_Lga'));
        //for checking the right selection either SG or LG
        const { statesGov, lgasGov } = this.state;

        return (
            <div>
                <Header />
                <div className="content-wrapper" style={{ minHeight: 1602 }}>
                    <Menu /><br />
                    {this.state.AdoptSurveyloading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                    <section className="content">
                        <div className="container-fluid">
                            <div className="row">
                                {/* left column */}
                                <div className="col-md-12">
                                   
                                <center>
                                                    {this.state.AdoptSurveymessage && (
                                                        <div className="pb-5" >
                                                            <div
                                                                className={
                                                                    this.state.AdoptSurveysuccessful
                                                                    ? "alert alert-success"
                                                                    : "alert alert-danger"
                                                                }
                                                                role="alert"
                                                            >
                                                                <h3 > {this.state.AdoptSurveymessage}</h3>
                                                            </div>
                                                        </div>
                                                    )}</center>
                                               
                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">Create Survey</h3>
                                        </div>
                                        <div className="card-body">

                                            <Form onSubmit={this.AdoptSurvey} ref={c => { this.form = c; }} >
                                                <div className="row">
                                                    <div className="col-sm-6">
                                                        <div className="form-group">
                                                            <label> Title</label>
                                                            <Input type="text" className="form-control " placeholder
                                                                value={this.state.title}
                                                                onChange={this.onChangeTitle}
                                                                validations={[required]}
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="col-sm-6">
                                                        <div className="form-group">
                                                            <label> Quarter</label>
                                                            <Select  className="form-control " placeholder
                                                                value={this.state.quarter}
                                                                onChange={this.onChangeQuarter}
                                                                validations={[required]}
                                                            >
                                                                <option value="">Select</option>
                                                                <option value="q1">Q1</option>
                                                                <option value="q2">Q2</option>
                                                                <option value="q3">Q3</option>
                                                                <option value="q4">Q4</option>
                                                                <option value="hyq1">HYQ1</option>
                                                                <option value="hyq2">HYQ2</option>
                                                                
                                                            </Select>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className="col-sm-6">
                                                        <div className="form-group">
                                                            {/* text input */}
                                                            <label> Year</label>
                                                            <Input type="number" className="form-control " placeholder
                                                                value={this.state.year}
                                                                onChange={this.onChangeYear}
                                                                validations={[required]}
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-sm-6">
                                                            {/* text input */}
                                                            <div className="form-group">
                                                                <label> Start date</label>
                                                                <Input type="date" format="YYYY-MM-DD" className="form-control " placeholder
                                                                    value={this.state.start_date}
                                                                    onChange={this.onChangeStartDate}
                                                                    validations={[required]}
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="col-sm-6">
                                                            <div className="form-group">
                                                                <label> Due Date</label>
                                                                <Input type="date" format="YYYY-MM-DD" className="form-control " placeholder
                                                                    value={this.state.due_date}
                                                                    onChange={this.onChangeDueDate}
                                                                    validations={[required]}
                                                                />
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-sm-6">
                                                        <div className="form-group">
                                                            <label> Survey Type</label>
                                                            <Select type="text" className="form-control " placeholder
                                                            validations={[required]}
                                                                value={this.state.survey_type}
                                                                onChange={this.onChangeSurveyType}                                                               
                                                            >
                                                                <option value="">Select</option>
                                                                <option value="sg">SG</option>
                                                                <option value="lg">LG</option>
                                                                <option value="other">OTHER</option>
                                                            </Select>
                                                        </div>
                                                    </div>
                                                </div>
                                                {statesGov && (
                                                    <div className="table-container">
                                                        <table className="table">
                                                            <thead>

                                                                <tr>
                                                                    <th>STATE</th>

                                                                    <th> Check all
                                                                     <MultiselectCheckbox
                                                                            checkboxes=".table .select-checkbox"
                                                                            sync=".table .row"
                                                                            onNotAllChecked={(selectedMap) => {
                                                                                const fieldsList = [];
                                                                                for (let i = 0; i < selectedMap.orderedByI().length; i++) {
                                                                                    fieldsList.push(selectedMap.orderedByI()[i].value);
                                                                                }
                                                                                //    const go = fieldsList.join()
                                                                                localStorage.setItem("Checked_State", fieldsList);
                                                                                // console.log(" me Checked_State",fieldsList);
                                                                                // console.log("Not all checked", selectedMap.orderedByI());  

                                                                            }}
                                                                            onAllChecked={(selectedMap) => {
                                                                                // console.log("All checked", selectedMap.orderedByI());
                                                                                const fieldsList = [];
                                                                                for (let i = 0; i < selectedMap.orderedByI().length; i++) {
                                                                                    fieldsList.push(selectedMap.orderedByI()[i].value);
                                                                                }
                                                                                //    const go = fieldsList.join()
                                                                                localStorage.setItem("Checked_State", fieldsList);
                                                                                // console.log(" me Checked_State",fieldsList);
                                                                            }}
                                                                            onAllUnchecked={(selectedMap) => {
                                                                                const fieldsList = [];
                                                                                for (let i = 0; i < selectedMap.orderedByI().length; i++) {
                                                                                    fieldsList.push(selectedMap.orderedByI()[i].value);
                                                                                }
                                                                                //    const go = fieldsList.replace(/\d+/g,"")
                                                                                   
                                                                                localStorage.setItem("Checked_State", fieldsList);
                                                                                // console.log(" me Checked_State",fieldsList);
                                                                                // console.log("All unchecked", selectedMap.orderedByI());

                                                                            }}

                                                                        />
                                                                    </th>
                                                                </tr>

                                                            </thead>
                                                            <tbody className="table-body">
                                                                {List_Of_States.map((state, i) => (

                                                                    <tr>
                                                                        <td>{state.title}</td>
                                                                        <td>
                                                                            <input
                                                                                type="checkbox"
                                                                                className="select-checkbox"
                                                                                value={`${state.pk}`}
                                                                                // onChange={this.onChangeState}
                                                                                validations={[required]}
                                                                            />
                                                                        </td>
                                                                    </tr>
                                                                ))}

                                                            </tbody>

                                                        </table>
                                                    </div>
                                                )}
                                                {lgasGov && (
                                                    <div className="table-container">
                                                        <table className="table">
                                                            <thead>
                                                                <tr>
                                                                    <th>LGA</th>

                                                                    <th> Check all
                                                                     <MultiselectCheckbox
                                                                            checkboxes=".table .select-checkbox"
                                                                            sync=".table .row"
                                                                            onNotAllChecked={(selectedMap) => {
                                                                                const fieldsList = [];
                                                                                for (let i = 0; i < selectedMap.orderedByI().length; i++) {
                                                                                    fieldsList.push(selectedMap.orderedByI()[i].value);
                                                                                }
                                                                                //    const go = fieldsList.join()
                                                                                localStorage.setItem("Checked_LGA", fieldsList);
                                                                                // console.log(" me Checked_LGA",fieldsList);
                                                                                // console.log("Not all checked", selectedMap.orderedByI());  

                                                                            }}
                                                                            onAllChecked={(selectedMap) => {
                                                                                // console.log("All checked", selectedMap.orderedByI());
                                                                                const fieldsList = [];
                                                                                for (let i = 0; i < selectedMap.orderedByI().length; i++) {
                                                                                    fieldsList.push(selectedMap.orderedByI()[i].value);
                                                                                }
                                                                                //    const go = fieldsList.join()
                                                                                localStorage.setItem("Checked_LGA", fieldsList);
                                                                                // console.log(" me Checked_LGA",fieldsList);
                                                                            }}
                                                                            onAllUnchecked={(selectedMap) => {
                                                                                const fieldsList = [];
                                                                                for (let i = 0; i < selectedMap.orderedByI().length; i++) {
                                                                                    fieldsList.push(selectedMap.orderedByI()[i].value);
                                                                                }
                                                                                //    const go = fieldsList.join()
                                                                                localStorage.setItem("Checked_LGA", fieldsList);
                                                                                // console.log(" me Checked_LGA",fieldsList);
                                                                                // console.log("All unchecked", selectedMap.orderedByI());

                                                                            }}

                                                                        />
                                                                    </th>
                                                                </tr>
                                                            </thead>
                                                            <tbody className="table-body">
                                                                {List_Of_lgas && List_Of_lgas.map(lga => (
                                                                    <tr>
                                                                        <td>{lga.title}</td>
                                                                        <td>
                                                                            <input
                                                                                type="checkbox"
                                                                                className="select-checkbox"
                                                                                value={`${lga.pk}`}
                                                                                // onChange={this.onChangeLgas}
                                                                                validations={[required]}
                                                                            />
                                                                        </td>
                                                                    </tr>
                                                                ))}

                                                            </tbody>
                                                        </table>
                                                    </div>
                                               )}
                                                <center>
                                                    <div className="row">
                                                        <button
                                                            disabled={this.state.AdoptSurveyloading}
                                                            className="btn btn-success btn-lg float-right"
                                                        >

                                                            {this.state.AdoptSurveyloading && (
                                                                <span className="spinner-grow spinner-grow-sm"></span>
                                                            )}
                                                            Create Survey
                                                        </button>
                                                    </div>
                                                </center>
                                                <CheckButton
                                                    style={{ display: "none" }}
                                                    ref={c => {
                                                        this.checkBtn = c;
                                                    }}
                                                />
                                                
                                            </Form>
                                       
                                        </div>
                                    </div>
                                    {/* /.card-body */}
                                </div>
                                {/* /.card */}
                            </div>
                            {/*/.col (right) */}
                            {/* /.row */}
                        </div>{/* /.container-fluid */}
                    </section>
                </div>
                <Footer />
            </div>
        );
    }
}

