export default class CreateNewUser extends Component {

  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);
    this.onChangeFirstName = this.onChangeFirstName.bind(this);
    this.onChangeLastName = this.onChangeLastName.bind(this);
    this.onChangeAddress = this.onChangeAddress.bind(this);
    this.onChangeLG = this.onChangeLG.bind(this);
    this.onChangeState = this.onChangeState.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.onChangeDob = this.onChangeDob.bind(this);
    this.onChangePhoneNumber = this.onChangePhoneNumber.bind(this);
    this.onChangeBranch = this.onChangeBranch.bind(this);
    this.onChangeDesignation = this.onChangeDesignation.bind(this);
    this.onChangeStaffNumber = this.onChangeStaffNumber.bind(this);
    
    this.state = {
      username: "",
      password: "",
      first_name: "",
      last_name: "",
      address: "",
      lga: "",
      states: "",
      dob: "",
      email: "",
      phone_number: "",
      successful: false,
      message: "",
      loading: false,
      Lgaloading:false,
      branch_pk:"",
      designation_pk:"",
      group_pk:6,
      staff_number:""

    };
  }


  componentDidMount() {
    
    const token = "bd7cfc4fa97ec7a61fff4dd69fad8f548f2d86dd";
    localStorage.setItem("token", JSON.stringify(token));

    //call the States and LGAS at the first 
    const state = "Ahmad loophole";
    axios.get(`https://gfso.chinikiguard.com/locations/api/list/state/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
        params:
        {
          state: state
        }
      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("List_Of_State", JSON.stringify(res.data));
        }
        this.setState({
          List_Of_State: res.data,
      });
        
        // console.log(res.data)                  

      })
      axios.get(`https://gfso.chinikiguard.com/locations/api/list/lga/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
        params:
        {
          state: state
        }
      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("List_Of_Lga", JSON.stringify(res.data));
        }
      }); 
      axios.get(`https://gfso.chinikiguard.com/accounts/api/branch/list/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
       
      },
    )
      .then(res => {
        this.setState({
          BranchList: res.data,
      });
      });
      axios.get(`https://gfso.chinikiguard.com/accounts/api/designation/list/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
       
      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("designations", JSON.stringify(res.data));
      }
        this.setState({
          DesignationList: res.data,
      });
      });

      axios.get(`https://gfso.chinikiguard.com/accounts/api/users/group/list/`,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
       
      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("designations", JSON.stringify(res.data));
      }
        this.setState({
          GroupList: res.data,
      });
      });



  }

  componentDidUpdate(prevProps, prevState) {

    // const GetUsers = JSON.parse(localStorage.getItem('Users'));
    const list_of_lga = JSON.parse(localStorage.getItem('List_Of_Lga'))
    const token = JSON.parse(localStorage.getItem('token'));

      // const Lgas = list_of_lga.filter(item => item.state === statepk);
      // localStorage.setItem("LGAS", JSON.stringify(Lgas));
     
      try {
        if (prevState.states !== this.state.states) {
          const statepk = this.state.states

          if(statepk){
            this.setState({
              
              Lgaloading: true
          });
          axios.get(`https://gfso.chinikiguard.com/locations/api/list/lga/`,
            {
              headers:
              {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
              },
              params:
              {
                state_pk: statepk
              }
            },
          )
            .then(res => {

                this.setState({
                  Lgaloading: false,
                  Lgas: res.data,
              });
            
            });
          }
          
        }
  
      } catch (e) {
  
        console.log(e);
      }
    
   

  }

  onChangeFirstName(e) {
    this.setState({
      first_name: e.target.value
    });
  }
  onChangeLastName(e) {
    this.setState({
      last_name: e.target.value
    });
  }
  onChangeAddress(e) {
    this.setState({
      address: e.target.value
    });
  }
  onChangeLG(e) {
    this.setState({
      lga: e.target.value
    });
  }
  onChangeState(e) {
    this.setState({
      states: e.target.value
    });
  }

  onChangeUsername(e) {
    this.setState({
      username: e.target.value
    });
  }

  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  onChangeDob(e) {
    this.setState({
      dob: e.target.value
    });
  }
  onChangePhoneNumber(e) {
    this.setState({
      phone_number: e.target.value
    });
  }
  onChangeDesignation(e) {
    this.setState({
      designation_pk: e.target.value
    });
  }
  onChangeBranch(e) {
    this.setState({
      branch_pk: e.target.value
    });
  }  
  onChangeStaffNumber(e) {
    this.setState({
      staff_number: e.target.value
    });
  }
  

  handleRegister(e) {
    e.preventDefault();
    this.setState({
      message: "",
      successful: false,
      loading: true
    });
    this.form.validateAll();
    if (this.checkBtn.context._errors.length === 0) {
      AuthService.createNewUser(
        this.state.username,
        this.state.password,
        this.state.first_name,
        this.state.last_name,
        this.state.address,
        this.state.lga,
        this.state.states,
        this.state.dob,
        this.state.email,
        this.state.phone_number,
        this.state.group_pk,
        this.state.designation_pk,
        this.state.branch_pk,
        this.state.staff_number
      ).then(
        response => {
          if (response.data) {
            localStorage.setItem("tempoUser", JSON.stringify(response.data));
            localStorage.setItem("token", JSON.stringify(response.data.token));
          }
          this.setState({
            message: response.data.message,
            successful: true
          });
          window.location = "/UserUploads"
          console.log(response.data)
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data.detail &&
              error.response.data.detail) ||
            error.message ||
            error.toString();

          this.setState({
            loading: false,
            message: resMessage
          });
        }
      );
    } else {
      this.setState({
        loading: false
      });
    }

  }

  render() {

    return (

      <div className="hold-transition login-page">
                {this.state.ViewTemplateloading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                <div className="">
                    <div className="login-logo">
                    <img src={Logo} height="100px" alt="" />
                        <br />
                        <Link to=""><b>CBN</b> GFSO</Link>
                    </div>
                    <div className="col-12">

                    <div className="card-">
                        <div className="card-body login-card-body">
                            {/* <p className="login-box-msg">Sign in to start your session</p> */}
                            <br />            
                <Form onSubmit={this.handleRegister} ref={c => { this.form = c; }} className="form" id="">
                  <div className="row">
                    <div className="col-sm-6">
                      {/* text input */}
                      <div className="form-group">
                        <label>First Name</label>
                        <Input type="text" id="firstname" className="form-control" placeholder="first name" value={this.state.first_name}
                          onChange={this.onChangeFirstName}
                          validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Last Name</label>
                        <Input type="text" id="lastname" className="form-control" placeholder="last name" value={this.state.last_name}
                          onChange={this.onChangeLastName}
                          validations={[required]} />
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-sm-6">
                      {/* textarea */}
                      <div className="form-group">
                        <label>Email</label>
                        <Input type="email" id="email" className="form-control" placeholder="Email" value={this.state.email}
                          onChange={this.onChangeEmail}
                          validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Phone Number</label>
                        <Input type="tel" id="phonenumber" className="form-control" placeholder="Phone number" value={this.state.phone_number}
                          onChange={this.onChangePhoneNumber}
                          validations={[required]} />
                      </div>
                    </div>
                  </div> 
                  <div className="row">
                    
                    <div className="col-sm-12">
                      <div className="form-group">
                        <label>Staff Number</label>
                        <Input type="number" id="staffnumber" className="form-control" 
                        placeholder="Staff number" value={this.state.staff_number}
                          onChange={this.onChangeStaffNumber}
                          validations={[required]} />
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-sm-6">
                      {/* text input */}
                      <div className="form-group">
                        <label>Address</label>
                        <Textarea type="text" id="address" className="form-control"
                          placeholder="Address" value={this.state.address}
                          onChange={this.onChangeAddress} validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">

                        <label>Date of birth</label>

                        <Input type="date" format="YYYY-MM-DD" className="form-control " placeholder
                          value={this.state.dob}
                          onChange={this.onChangeDob}
                          validations={[required]}

                        />
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>State</label>
                        <Select value={this.state.states}
                          onChange={this.onChangeState}
                          validations={[required]} type="text" id="state" className="form-control">
                          <option value>Select</option>
                          {this.state.List_Of_State&&this.state.List_Of_State.map(state => (

                            <option value={state.pk}>{state.title}</option>

                          ))}

                        </Select>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Lga</label>
                        {this.state.Lgaloading && (
                        <span className="spinner-grow spinner-grow-sm"></span>
                      )}
                        <Select value={this.state.lga}
                          onChange={this.onChangeLG}
                          validations={[required]} type="text" id="state" className="form-control">
                          
                          <option > Select  </option>
                          {this.state.Lgas && this.state.Lgas.map(lga => (
                            <option value={lga.pk}>{lga.title}</option>
                          ))}
                        </Select>
                       

                      </div>
                    </div>
                  </div>
                  <div className="row">
                  <div className="col-sm-6">

                  <div className="form-group">
                  <label>Branch</label>

                  <Select
                    className="form-control"
                    id="exampleFormControlSelect9"
                    validations={[required]}
                    onChange={this.onChangeBranch}
                    value={this.state.branch_pk}
                  >
                    <option value="">--select--</option>

                    {this.state.BranchList && this.state.BranchList.map(branch => (

                      <option value={branch.pk}>{branch.name}</option>

                    ))}

                  </Select>
                  </div>
                  </div>
                  <div className="col-sm-6">

                   <div className="form-group">
                   <label>Designation</label>

                  <Select
                    className="form-control"
                    id="exampleFormControlSelect9"
                    validations={[required]}
                    onChange={this.onChangeDesignation}
                    value={this.state.designation_pk}
                  >
                    <option value="">--select--</option>
                    {this.state.DesignationList && this.state.DesignationList.map(Designation => (
                      <option value={Designation.pk}>{Designation.title}</option>
                    ))}
                  </Select>
                  </div>
                  </div>
                  </div>                                
                  <div className="row">
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Username</label>
                        <Input type="text" name="username" className="form-control" placeholder="Username"
                          value={this.state.username}
                          onChange={this.onChangeUsername}
                          validations={[required]} />
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <label>Password</label>
                        <Input type="password" name="password" className="form-control" placeholder="password" value={this.state.password}
                          onChange={this.onChangePassword}
                          validations={[required]} />
                      </div>
                    </div>
                  </div>
                  <div className="form-group">
                    <button
                      className="btn btn-primary btn-lg float-right"
                      disabled={this.state.loading}
                    >
                      {this.state.loading && (
                        <span className="spinner-border spinner-border-sm"></span>
                      )}
                      <span>Register</span>
                    </button>
                  </div>
                  <center>
                    {this.state.message && (
                      <div className="form-group">
                        <div
                          className={
                            this.state.successful
                              ? "alert alert-success"
                              : "alert alert-danger"
                          }
                          role="alert"
                        >
                          {this.state.message}
                        </div>
                      </div>
                    )}
                  </center>
                  <CheckButton
                    style={{ display: "none" }}
                    ref={c => {
                      this.checkBtn = c;
                    }}
                  />

                </Form>
              
                        </div>

                        </div>
                        </div>

                </div>
            </div>

     

    );
  }
}